@import url('https://fonts.googleapis.com/css?family=Audiowide|Germania+One|Poiret+One');
.App {
    text-align: center;
}

.App-logo {
    -webkit-animation: App-logo-spin infinite 20s ease-in-out;
            animation: App-logo-spin infinite 20s ease-in-out;
    height: 80px;
    width: 50vw;
}

.App-header {
    background-image: -webkit-radial-gradient(circle, #3241a6 0, #202a6b 120%);
    background-image: -o-radial-gradient(circle, #3241a6 0, #202a6b 120%);
    background-image: radial-gradient(circle, #3241a6 0, #202a6b 120%);
    height: 150px;
    padding: 20px;
    color: #ff9465;
}

.App-title {
    font-size: 1.5em;
}

.App-intro {
    font-size: large;
}

@-webkit-keyframes App-logo-spin {
    100% {
        -webkit-transform: translate(0, 0);
                transform: translate(0, 0);
    }
    75% {
        -webkit-transform: translate(50px, 37.5px);
                transform: translate(50px, 37.5px);
    }
    50% {
        -webkit-transform: translate(0, 75px);
                transform: translate(0, 75px);
    }
    25% {
        -webkit-transform: translate(-50px, 37.5px);
                transform: translate(-50px, 37.5px);
    }
    0% {
        -webkit-transform: translate(0, 0);
                transform: translate(0, 0);
    }
}

@keyframes App-logo-spin {
    100% {
        -webkit-transform: translate(0, 0);
                transform: translate(0, 0);
    }
    75% {
        -webkit-transform: translate(50px, 37.5px);
                transform: translate(50px, 37.5px);
    }
    50% {
        -webkit-transform: translate(0, 75px);
                transform: translate(0, 75px);
    }
    25% {
        -webkit-transform: translate(-50px, 37.5px);
                transform: translate(-50px, 37.5px);
    }
    0% {
        -webkit-transform: translate(0, 0);
                transform: translate(0, 0);
    }
}
.content-container {
    width: 100%;
    background-color: #fefefe;
}
.language {
    font-style: italic;
}
.file-name {
    font-style: italic;
}
.content {
    width: 100%;
    margin: 40px auto;
    text-align: left;
    background-color: #eee;
    padding: 30px;
}
.sub-content {
    margin: 0 auto;
    width: 95%;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
.language-sub-content {
    margin: 0 auto;
    width: 95%;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
pre {
    background-color: #ddd;
    margin-left: 15px;
    margin-right: 15px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    padding: 30px;
}
.language-subcontent-header,
.file-name-header {
    font-family: 'Poiret One', cursive;
    font-size: 1.2em;
}
.subcontent-header {
    font-family: 'Poiret One', cursive;
    font-size: 1.6em;
}
.content-header {
    font-family: 'Audiowide', cursive;
    font-style: bold;
    font-size: 2em;
}
.directory-button {
    width: 25%;
    display: inline-block;
    background-color: #eee;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    padding: 10px;
    margin: 2px;
}
.directory-button:hover {
    cursor: pointer;
    background-color: #ddd;
}
.directory-choice {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    padding: 10px;
    width: 25%;
    text-align: center;
    display: inline-block;
    color: black;
    all: none;
}
.directory-choice:hover {
    background-color: #ddd;
    color: grey;
}
.directory-choice:visited,
.directory-choice:active {
    all: none;
    color: black;
}
.directory-choice-container {
    width: 75%;
    margin: 0 auto;
    height: 150px;
}



